import { getLogger, IWalletStore, SessionWallet, Wallet, WalletManager, WalletType, WebWallet } from 'vitepunks-common';
import { ClientConstants } from '../common/constants';

const logger = getLogger()

export class WalletStore implements IWalletStore {

  private readonly _webWalletKey = ClientConstants.WEB_WALLET_STORAGE_SPACE;
  private readonly _sessionWalletKey = ClientConstants.SESSION_WALLET_STORAGE_SPACE

  clear(): void {
    localStorage.removeItem(this._webWalletKey);
    sessionStorage.removeItem(this._sessionWalletKey);
  }

  getItem(): Maybe<WebWallet | SessionWallet> {
    let data;

    try {
      data = localStorage.getItem(this._webWalletKey);
      if (!data) {
        data = sessionStorage.getItem(this._sessionWalletKey);
      }
    } catch (err) {
      logger.error(err)();
      return undefined;
    }

    if (!data) {
      return undefined;
    }

    try {
      return Wallet.fromJS(JSON.parse(data));
    } catch (err) {
      logger.error(err)();
      return undefined;
    }
  }

  setItem(data: WebWallet | SessionWallet): void {
    const saveData = JSON.stringify(data);

    try {
      switch (data.type) {
        case WalletType.Web:
          localStorage.setItem(this._webWalletKey, saveData);
          break;
        case WalletType.Session:
          sessionStorage.setItem(this._sessionWalletKey, saveData);
          break;
        default:
          throw new Error(`Wallet type '${data.type}' is not supported.`)
      }
    } catch (err) {
      logger.error(err)();
    }
  }
}

const manager = new WalletManager(new WalletStore());

export const getWalletManager = () => {
  return manager;
}