import { FileUtil } from "vitepunks-common";

export class BrowserFileUtil extends FileUtil {
  async readFileAsync(path: string, encoding: BufferEncoding = 'utf8'): Promise<any> {
    const response = await fetch(path);
    switch (encoding) {
      case 'binary':
        return response.arrayBuffer();
      default:
        return response.text();
    }
  }
  async writeFileAsync(path: string, data: any, encoding?: BufferEncoding): Promise<any> {

  }
}

const browserFileUtil = new BrowserFileUtil();

export const getBrowserFileUtil = () => {
  return browserFileUtil;
}