import { CommonConfig, CommonUtil, CompressUtil, FileUtil, PunksBaseContract } from "vitepunks-common";
import { getBrowserFileUtil } from "../util/file.util";

export class PunksContract extends PunksBaseContract {
  private readonly _fileUtil: FileUtil;

  constructor() {
    super();
    this._fileUtil = getBrowserFileUtil();
  }

  protected async initContractAsyncProtected(basePath: string): Promise<void> {
    const punksContract = await this._fileUtil.readFileAsync(CommonUtil.appendPath(basePath, "Punks.gz?v=" + process.env.REACT_APP_VERSION), "binary");
    this._contract = JSON.parse(CompressUtil.decompress(CommonUtil.toUint8Array(punksContract, "binary")));
    this._contract.address = CommonConfig.CONTRACT_ADDRESS;
  }
}

const contract = new PunksContract();

export const getPunksContract = () => {
  return contract;
}