import { ICommonConfig, PunkTypes } from "vitepunks-common";

export class ClientConfig implements ICommonConfig {
  private _useIpfs: boolean = false;
  get USE_IPFS(): boolean {
    return this._useIpfs;
  }
  set USE_IPFS(_flag: boolean) {
    this._useIpfs = _flag;
  }
  get IS_PRODUCTION(): boolean {
    if (!process.env.REACT_APP_IS_PRODUCTION) {
      throw new Error("REACT_APP_IS_PRODUCTION is not defined");
    }
    return process.env.REACT_APP_IS_PRODUCTION === "true";
  }
  get CONTRACT_ADDRESS(): string {
    if (!process.env.REACT_APP_CONTRACT_ADDRESS) {
      throw new Error("REACT_APP_CONTRACT_ADDRESS is not defined");
    }
    return process.env.REACT_APP_CONTRACT_ADDRESS;
  }
  get PRE_MINT_HEIGHT(): Maybe<PunkTypes.BigNumberWrapper> {
    if (!process.env.REACT_APP_PRE_MINT_HEIGHT) {
      return null;
    }
    return new PunkTypes.BigNumberWrapper(process.env.REACT_APP_PRE_MINT_HEIGHT);
  }
  get EXPLORER_URL(): string {
    if (!process.env.REACT_APP_EXPLORER_URL) {
      throw new Error("REACT_APP_EXPLORER_URL is not defined");
    }
    return process.env.REACT_APP_EXPLORER_URL;
  }
  get ENABLE_LOGGER(): boolean {
    if (!process.env.REACT_APP_ENABLE_LOGGER) {
      throw new Error("REACT_APP_ENABLE_LOGGER is not defined");
    }
    return process.env.REACT_APP_ENABLE_LOGGER === "true";
  }
  get ENABLE_MNEMONIC_LOGIN(): boolean {
    if (!process.env.REACT_APP_ENABLE_MNEMONIC_LOGIN) {
      throw new Error("REACT_APP_ENABLE_MNEMONIC_LOGIN is not defined");
    }
    return process.env.REACT_APP_ENABLE_MNEMONIC_LOGIN === "true";
  }
  get NETWORK_ID(): number {
    if (!process.env.REACT_APP_NETWORK_ID) {
      throw new Error("REACT_APP_NETWORK_ID is not defined");
    }
    return parseInt(process.env.REACT_APP_NETWORK_ID);
  }
  get BASENAME(): Maybe<string> {
    return process.env.REACT_APP_BASENAME;
  }
}

const config = new ClientConfig();

export const getClientConfig = () => {
  return config;
}