import { PunkTypes } from "vitepunks-common";

export class NetworkManager {
  private _networkHeight: PunkTypes.BigNumberWrapper;
  private _network?: Maybe<PunkTypes.Network>;
  private _setNetworkCallback?: (network?: Maybe<PunkTypes.Network>) => void;

  constructor() {
    this._networkHeight = new PunkTypes.BigNumberWrapper("0");
  }

  set networkHeight(height: PunkTypes.BigNumberWrapper) {
    this._networkHeight = height;
  }

  get networkHeight(): PunkTypes.BigNumberWrapper {
    return this._networkHeight;
  }

  set onSetNetworkCallback(cb: (network?: Maybe<PunkTypes.Network>) => void) {
    this._setNetworkCallback = cb;
  }

  getNetwork(): Maybe<PunkTypes.Network> {
    return this._network;
  }

  setNetwork(network?: Maybe<PunkTypes.Network>): void {
    this._network = network;
    if (this._setNetworkCallback) {
      this._setNetworkCallback(this._network);
    }
  }
}

const manager = new NetworkManager();

export const getNetworkManager = () => {
  return manager;
}