import { faBan, faDonate, faEraser, faExchangeAlt, faGift, faHandHoldingUsd, faPowerOff, faQuestion, faTag, faTrophy } from '@fortawesome/free-solid-svg-icons';
import { PunkTypes } from 'vitepunks-common';

export abstract class IconUtil {
  static getBuyEventTypeIcon = () => {
    return faDonate;
  }
  static getClaimEventTypeIcon = () => {
    return faExchangeAlt;
    //return faBoxOpen;
  }
  static getOfferEventTypeIcon = () => {
    return faTag;
  }
  static getGiftEventTypeIcon = () => {
    return faGift;
  }
  static getCancelSaleEventTypeIcon = () => {
    return faPowerOff;
  }
  static getCancelGiftEventTypeIcon = () => {
    return faEraser;
  }
  static getPunkEventTypeIcon = (eventType: PunkTypes.PunkEventType, amount?: Maybe<PunkTypes.BigNumberWrapper>) => {
    switch (eventType) {
      case PunkTypes.PunkEventType.Mint:
        return faTrophy;
      case PunkTypes.PunkEventType.Offer:
        if (amount && amount.gt(0)) {
          return IconUtil.getOfferEventTypeIcon();
        }
        return IconUtil.getGiftEventTypeIcon();
      case PunkTypes.PunkEventType.NewBid:
        return faHandHoldingUsd;;
      case PunkTypes.PunkEventType.CancelBid:
        return faBan;
      case PunkTypes.PunkEventType.Buy:
        if (amount && amount.gt(0)) {
          return IconUtil.getBuyEventTypeIcon();
        }
        return IconUtil.getClaimEventTypeIcon();
      case PunkTypes.PunkEventType.CancelOffer:
        if (amount && amount.gt(0)) {
          return IconUtil.getCancelSaleEventTypeIcon();
        }
        return IconUtil.getCancelGiftEventTypeIcon();
      default:
        return faQuestion;
    }
  }
  static getAccountAuctionTypeIcon = (auctionType: PunkTypes.AccountAuctionType, amount?: Maybe<PunkTypes.BigNumberWrapper>) => {
    switch (auctionType) {
      case PunkTypes.AccountAuctionType.Bid:
        return IconUtil.getPunkEventTypeIcon(PunkTypes.PunkEventType.NewBid, amount);
      case PunkTypes.AccountAuctionType.Offer:
        return IconUtil.getPunkEventTypeIcon(PunkTypes.PunkEventType.Offer, amount);
      default:
        return IconUtil.getPunkEventTypeIcon(PunkTypes.PunkEventType.Unknown, amount);
    }
  }
}