import { BaseDataSource, CommonConstants, CommonUtil, FileUtil, getLogger, PunkTypes, TransformUtil, WalletManager } from "vitepunks-common";
import { getWalletManager } from "../storage/wallet.storage";
import { getBrowserFileUtil } from "../util/file.util";

const logger = getLogger();

export class MockDataSource extends BaseDataSource {
  private _events!: PunkTypes.PunkEvent[];

  constructor() {
    super();
    this.clearMock();
  }

  private clearMock(): void {
    this._events = [];
  }

  protected get fileUtil(): FileUtil {
    return getBrowserFileUtil();
  }
  protected get walletManager(): WalletManager {
    return getWalletManager();
  }

  protected async initPunkInfosAsync(): Promise<void> {
    let infos = await this.fileUtil.readFileAsync("./assets/data/mock_infos.json");
    infos = JSON.parse(infos);

    this._punkInfos = this.getDefaultPunkInfos();
    if (infos) {
      for (const p of infos.punks) {
        const info = TransformUtil.toPunkInfo(p);
        if (info) {
          const index = parseInt(p.id) - 1;
          this._punkInfos[index] = info;
        }
      }
    }
  }

  async initAsync(network: PunkTypes.Network): Promise<void> {
    logger.info('Init MockDataSource')();
    this.clearMock();

    let events = await this.fileUtil.readFileAsync("./assets/data/mock_events.json");
    events = JSON.parse(events);
    for (let index = 0; index < events.length; index++) {
      const e = events[index];
      this._events.push(TransformUtil.toPunkEvent(e));
    }
    this._events = this._events.reverse();
  }

  protected disposeProtected(): void {
    logger.info('Dispose MockDataSource')();
    this.clearMock();
  }

  async getQuotaAsync(_address: string): Promise<PunkTypes.ViteQuotaResult> {
    return {
      currentQuota: new PunkTypes.BigNumberWrapper("1000"),
      maxQuota: new PunkTypes.BigNumberWrapper("2000"),
      stakeAmount: new PunkTypes.BigNumberWrapper("10000")
    };
  }

  async getPunkEventsAsync(_id: string, _limit: number, _offset: number, _height?: Maybe<string>): Promise<PunkTypes.PunkEvents> {
    logger.info("id", _id, "limit", _limit, "offset", _offset, "height", _height)();
    await CommonUtil.timeout(2000);
    if (!CommonUtil.isNumber(_id)) {
      return {
        blockHeight: TransformUtil.defaultBigNumber,
        events: []
      };
    }
    return {
      blockHeight: TransformUtil.defaultBigNumber,
      events: this._events.filter(e => e.punkId === parseInt(_id)).slice(_offset, _offset + _limit)
    };
  }

  async getPunkOwnerAsync(_id: string): Promise<Maybe<string>> {
    await CommonUtil.timeout(2000);
    if (!CommonUtil.isNumber(_id)) {
      return undefined;
    }
    const e = this._events.find(e => e.punkId.toString() === _id && e.eventType === PunkTypes.PunkEventType.Mint);
    if (!e) {
      return undefined;
    }
    return e.toAddress;
  }

  async getPunksByAddressAsync(_address: string, _limit: number, _offset: number): Promise<PunkTypes.Punk[]> {
    logger.info("_address", _address, "limit", _limit, "offset", _offset)();
    const punks = [
      await this.getPunkAsync("101"),
      await this.getPunkAsync("201"),
      await this.getPunkAsync("301"),
      await this.getPunkAsync("401"),
      await this.getPunkAsync("501"),
      await this.getPunkAsync("601"),
      await this.getPunkAsync("701"),
      await this.getPunkAsync("801"),
      await this.getPunkAsync("9999")
    ];
    return punks.slice(_offset, _offset + _limit);
  }

  async getMarketBidAsync(_id: string): Promise<Maybe<PunkTypes.MarketBid>> {
    await CommonUtil.timeout(1000);
    if (!CommonUtil.isNumber(_id)) {
      return undefined;
    }
    const e = this._events.find(e => e.punkId.toString() === _id && e.eventType === PunkTypes.PunkEventType.NewBid);
    if (!e) {
      return undefined;
    }
    return {
      __typename: PunkTypes.TypeNames.MarketBid,
      itemIndex: TransformUtil.toBigNumber(_id),
      bidder: e.fromAddress ?? CommonConstants.ZERO_ADDRESS,
      amount: e.amount ?? TransformUtil.defaultBigNumber,
      timestamp: null
    }
  }

  async getMarketOfferAsync(_id: string): Promise<Maybe<PunkTypes.MarketOffer>> {
    await CommonUtil.timeout(1000);
    if (!CommonUtil.isNumber(_id)) {
      return undefined;
    }
    const e = this._events.find(e => e.punkId.toString() === _id && e.eventType === PunkTypes.PunkEventType.Offer);
    if (!e) {
      return undefined;
    }
    const seller = await this.getPunkOwnerAsync(_id);
    return {
      __typename: PunkTypes.TypeNames.MarketOffer,
      itemIndex: TransformUtil.toBigNumber(_id),
      seller: seller ?? CommonConstants.ZERO_ADDRESS,
      minAmount: e.amount ?? TransformUtil.defaultBigNumber,
      onlySellTo: e.toAddress,
      timestamp: null
    }
  }

  async getMarketEventsAsync(_limit: number, _offset: number, _height?: Maybe<string>): Promise<PunkTypes.PunkEvents> {
    logger.info("limit", _limit, "offset", _offset)();
    await CommonUtil.timeout(500);
    return {
      blockHeight: new PunkTypes.BigNumberWrapper("0"),
      events: this._events.slice(_offset, _offset + _limit)
    }
  }

  async getMarketInfoAsync(): Promise<PunkTypes.MarketInfo> {
    logger.info("getMarketInfoAsync")();
    return {
      marketStarted: true,
      mintsRemaining: CommonConstants.TOTAL_MINTS - this._events.filter(e => e.eventType === PunkTypes.PunkEventType.Mint).length,
      mintPrice: new PunkTypes.BigNumberWrapper("12").shiftedBy(18),
      actionPrice: new PunkTypes.BigNumberWrapper("3.1").shiftedBy(18),
    };
  }

  async mintAsync(): Promise<boolean> {
    throw new Error("Method 'mintAsync' not implemented.");
  }

  async offerAsync(_id: string, _tokenId: string, _amount: string, _address?: string | null): Promise<boolean> {
    logger.info("offerAsync", "id", _id, "tokenId", _tokenId, "amount", _amount, "address", _address)();
    await CommonUtil.timeout(2000);
    return true;
  }

  async cancelOfferAsync(_id: string): Promise<boolean> {
    logger.info("cancelOfferAsync", "id", _id)();
    await CommonUtil.timeout(2000);
    return true;
  }

  async buyAsync(_id: string, _amount: string): Promise<boolean> {
    logger.info("buyAsync", "id", _id, "amount", _amount)();
    await CommonUtil.timeout(2000);
    return true;
  }

  async bidAsync(_id: string, _amount: string): Promise<boolean> {
    logger.info("bidAsync", "id", _id, "amount", _amount)();
    await CommonUtil.timeout(2000);
    return true;
  }

  async acceptBidAsync(_id: string, _amount: string): Promise<boolean> {
    logger.info("acceptBidAsync", "id", _id, "amount", _amount)();
    await CommonUtil.timeout(2000);
    return true;
  }

  async cancelBidAsync(_id: string): Promise<boolean> {
    logger.info("cancelBidAsync", "id", _id)();
    await CommonUtil.timeout(2000);
    return true;
  }
}

const ds = new MockDataSource();

export const getMockDataSource = () => {
  return ds;
}