import { FileUtil, PunksBaseContract, ViteBaseDataSource, WalletManager } from "vitepunks-common";
import { getPunksContract } from "../contracts/punks.contract";
import { getWalletManager } from "../storage/wallet.storage";
import { getBrowserFileUtil } from "../util/file.util";

export class ViteDataSource extends ViteBaseDataSource {
  protected get contract(): PunksBaseContract {
    return getPunksContract();
  }
  protected get fileUtil(): FileUtil {
    return getBrowserFileUtil();
  }
  protected get walletManager(): WalletManager {
    return getWalletManager();
  }
}

const ds = new ViteDataSource();

export const getViteDataSource = () => {
  return ds;
}